import React from 'react'
import { Button, Modal } from 'react-bootstrap'
import { useHistory } from 'react-router'
import { Link } from 'react-router-dom'

export const SuccessMessage = ({
    show, onHide
}) => {
    const history = useHistory()
    return (
        <Modal
        show={show}
        onHide={onHide}
        size="lg"
        centered
      >
        <Modal.Header >
          <Modal.Title id="contained-modal-title-vcenter">
               !
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
                    {' '}
            <Link to="/purchases">
                !
            </Link>
 
        </Modal.Body>
        <Modal.Footer>
            <Button variant="outline-primary" onClick={()=> history.push('/')}> </Button>
            <Button variant="primary" onClick={() => history.push('/purchases')}>  </Button>
        </Modal.Footer>
      </Modal>
    )
}
