import React from 'react'
import { observer } from 'mobx-react-lite'
import { Button, Form, Modal, Dropdown, Row } from 'react-bootstrap'

const types = {
    create: {color: "info", title: ""},
    update: {color: "warning", title: ""},
    delete: {color: "danger", title: ""},
    issue:  {color: "primary", title: " "},
}
//template func component, for deleting data by requests on server
export const Crud = observer(({
    show, onHide, changeItem, title, afterChange, children, setItem, item, crudType
}) => {
    const [uploadStatus, setUploadStatus] = React.useState('')
    const type = types[crudType]

    React.useEffect(()=> {
      if (!show) setUploadStatus('')
    }, [show]) 

    const  handleChangeItem = async () => {
      try {
        setUploadStatus(" ...")
        const itemResponse = await changeItem(item)
        setItem(null)
        setUploadStatus(` ! `)
        await afterChange()
      } catch (e) {
        // setUploadStatus(e)
        setUploadStatus(e.response?.data.message)
      }
    } 

    return (
        <Modal
        show={show}
        onHide={onHide}
        size="lg"
        centered
        className=""
      >
        <Modal.Header >
          <Modal.Title id="contained-modal-title-vcenter">
            {type.title} {title}
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
            <Form>
                {children}
            </Form>
        </Modal.Body>
        <Modal.Footer>
          <p>
              {uploadStatus}    
          </p>
            <Button variant="outline-danger" onClick={onHide}></Button>
            <Button variant={type.color} onClick={() =>handleChangeItem()}>{type.title}</Button>
        </Modal.Footer>
      </Modal>
    )
})
