import React from 'react'
import { Alert, Button, Dropdown, Form } from 'react-bootstrap'
import { fetchOnePurchase, updatePurchase } from '../../../http/purchaseAPI'
import { Crud } from '../Crud'




export const DeviceIssue = ({show, onHide}) => {
    const [purchaseKey, setPurchaseKey] = React.useState('') 
    const [purchase, setPurchase] = React.useState()
    const [isSuccess, setIsSuccess] = React.useState(false)
    const [newPurchaseStatus, setNewPurchaseStatus] = React.useState(false)

    const [message, setMessage] = React.useState('')

    const purchaseField = [
        { label: ' ', value: purchase?.user?.email },
        { label: ' ', value: purchase?.device?.name },
        { label: ' ', value: purchase?.count },
        { label: ' ', value: new Date(purchase?.createdAt).toLocaleString() },
    ]

    React.useEffect(()=>{
        if (isSuccess) {
            setPurchase()
        }
    },[isSuccess])


    const  handleFetchPurchase = async () => {
        setIsSuccess(false)
        setPurchase()
        if (!purchaseKey.length) return setMessage('    !')
        const p = await fetchOnePurchase(purchaseKey)
        if (!p) return setMessage('      !')
        if (p.isIssued) return setMessage('   !')
        setPurchase(p)
        setMessage('')
    }

    const  handleChangeStatus = async (p) => {
        if (newPurchaseStatus !== true) {
            setMessage('    !')
            return
        }
        await updatePurchase(p)
    }

    return (
        <Crud
            show={show} 
            onHide={onHide} 
            changeItem={handleChangeStatus} 
            afterChange={()=>setIsSuccess(true)} 
            item={{...purchase, isIssued: newPurchaseStatus}}
            setItem={(val) => { setPurchase(val); setNewPurchaseStatus(false) }}
            crudType="issue"
        >
            <Form.Label>
                 
            </Form.Label>
            <Form.Control
                value={purchaseKey}
                onChange={(e) => setPurchaseKey(e.target.value)}
                placeholder={ `  ...`} 
            >
            </Form.Control>
            <Button 
                className="mt-2"
                onClick={handleFetchPurchase}
                variant="info"
            >
                 
            </Button>
            <hr/>
            {isSuccess&&!message.length
            &&  <Alert variant="success">
                      !
                </Alert>
            }

            {purchase 
            &&  
            <>
                {purchaseField.map((item, it) => 
                    <div key={it}>
                        <Form.Label>{item.label}</Form.Label>
                        <Form.Control disabled value={item.value} />
                    </div>
                )}

                <hr/>
               
                <Form.Check
                    className="mt-2 text-primary"
                    type='checkbox'
                    label={` `}
                    size="lg"
                    id="issueCheck"
                    value={newPurchaseStatus}
                    onClick={()=>setNewPurchaseStatus(true) }
                />
                
            </>
            }

            {message 
            && <Alert className="mt-2" variant="warning">{message}</Alert>
            }
        </Crud>
    )
}

